




























































































@import "element-ui/packages/theme-chalk/src/mixins/mixins.scss"

@include b(radio)
  @include e(input)
    @include when(checked)
      .el-radio__inner
        border-color: var(--color)
        background: var(--color)

  @include e(inner)
    border-color: var(--color)
    &:focus, &:hover
      border-color: var(--color)
      box-shadow: 0 0 2px var(--color)
