























































































































































































































































































table
  width: 100%
  max-width: 100%
  table-layout: fixed
  user-select: none
  touch-action: none

caption
  font-size: 12px
  color: #909399

th
  padding: 12px
  color: #909399
  font-weight: 500
  table.selectable &
    color: #606266

thead th
  text-align: center
  vertical-align: middle

tbody th
  text-align: right
  padding: 0 12px 24px 12px
  vertical-align: top
  span
    position: relative
    top: -0.5em

td
  border: 1px solid #ebeef5
