









































@import '@/assets/colors.sass'

td
  font-size: smaller
  text-align: center
  overflow: hidden
  white-space: nowrap
  text-overflow: ellipsis
  padding: 12px

  &.dark-background
    color: $inverted-foreground-color

  table.selectable &.selected,
  table.selectable &:not(.occupied)
    cursor: pointer
